$indigo: #5603ad;
$purple: #8965e0;
$pink: #f3a4b5;
$red: #f5365c;
$orange: #fb6340;
$yellow: #ffd600;
$green: #2dce89;
$teal: #11cdef;
$cyan: #2bffc6;
$white: #fff;
$gray: #8898aa;
$gray-dark: #32325d;
$light: #ced4da;
$lighter: #e9ecef;
$primary: #5e72e4;
$secondary: #f7fafc;
$success: #2dce89;
$info: #11cdef;
$warning: #fb6340;
$danger: #f5365c;
$light: #adb5bd;
$dark: #212529;
$default: #172b4d;
$white: #fff;
$neutral: #fff;
$darker: black;

$a-color-gray-light: rgba(179,178,177,1);
$a-color-gray-dark: rgba(112,111,111,1);

$a-color-blue: rgba(0,175,224,1);
$a-color-green: rgba(112,212,75,1);
$a-color-red: rgba(230,66,122,1);

//Color apply
$border-color-normal: rgba($a-color-gray-light,.5);
$border-color-active: rgba($a-color-blue,.5);
$border-color-hover: rgba($a-color-gray-light,.5);

$btn-secondary-color-normal: rgba($a-color-gray-light,.25);
$btn-secondary-color-active: rgba($a-color-gray-light,.5);
$btn-secondary-color-hover: rgba($a-color-gray-light,.5);



//Bootstrap
$primary: $a-color-blue !important;

$body-color: $a-color-gray-dark;
$body-bg-color: white; // #f8f9fe; #FCFCFB

$sidebar-color: gray;
$sidebar-bg-color: white;

//Font Size
$fs-xxxs: 	.500rem; //8px
$fs-xxs: 		.625rem; //10px
$fs-xs: 			.750rem; //12px
$fs-s: 				.875rem; //14px
$fs-md: 			1.00rem; //16px

//
.accent-color-primary{
	color: $a-color-blue;
}

.bg-gray-light{ background-color: $a-color-gray-light;}
.bg-gray-dark{ background-color: $a-color-gray-light;}

.bg-blue{ background-color: $a-color-blue}
.bg-green{ background-color: $a-color-green}
.bg-red{ background-color: $a-color-red}

// FONT
.caption{
	font-size: .75rem;
	display: block;
}

html{
	font-size: 16px;
	height: 100%;
	-webkit-font-smoothing: antialiased;
	body{
		color: $a-color-gray-dark;
		font-size: 1rem;
		background-color: $body-bg-color;
		// opacity: 0;
		transition: all .1s ease;
		
		.main-content{
			opacity: 0;
			transform: translateY(5%);
			transition: opacity .075s ease, transform .35s ease;
		}

		&.pace-done{
			transition: all .1s ease;
			// opacity: 1;
		
			.main-content{
				opacity: 1;
				transform: translateY(0);
				transition: opacity .075s ease, transform .35s ease;
			}

		}
	}
}

aside{
	background: $sidebar-bg-color;
	.dashboard-aside{
		padding: 1rem 2rem 0 1rem;
		width: 100%;
		position: sticky;
		top: 0;
		left: 0;
		z-index: 999;
		a{
			display: block;
			text-align: left;
		}
	}
	.navbar-brand-img{
		margin: 0rem 0 3rem ;
		padding: .25rem 0;
		width: 2.5rem;
	}
	nav{
		ul{
			padding: 0;
		}
		ul,li{
			list-style: none;
		}
		li{
			margin-bottom: .5rem;
		}
	}
	.nav-label{
		padding: 1rem 0;
		color: $a-color-gray-light;
		font-size: .75rem;
		letter-spacing: .5px;
	}
	.btn{
		color: $a-color-gray-dark;
		&.btn-active{
			background-color: rgba($a-color-blue,.15);
			color: $a-color-blue;
		}
		&:hover{
			box-shadow: 0 !important;
			background-color: rgba($a-color-gray-light,.15) !important;
		}
	}
}

header{
	background-color: white;
	&.main-header{
		position: sticky;
		top: 0;
		z-index: 99;
		box-shadow: -6px -6px 24px rgba(255, 255, 255, 0.25), 6px 6px 24px rgba(78, 84, 113, 0.05);
		.btn-notification{
			width: 3rem;
			height: 3rem;
			border-radius: 5px;
			border: 1px solid  $border-color-normal;
			padding: 0;
			text-align: center;
			padding: 0.5rem 0 0;
			color: $a-color-gray-dark;
			font-size: 1.25rem;
			margin-right: 1rem;
		}
		.btn-useraccount{
			border-radius: 100%;
			width: 3rem;
			height: 3rem;
			position: relative;
			overflow: hidden;
			display: flex;
			img{
				width: 100%;
			}
		}
	}
	&.project-header{
		position: sticky;
		top: 0;
		z-index: 99;
		box-shadow: -6px -6px 24px rgba(255, 255, 255, 0.25), 6px 6px 24px rgba(78, 84, 113, 0.05);
		.btn{
			box-shadow: none;
			background-color: $btn-secondary-color-normal;
		}
		.project-name{
				margin: 0 1rem;
			input{
				padding: 0.25rem 0.5rem;
				margin-left: .5rem;
				border: 0;
				border-bottom: 1px solid $border-color-normal;
				text-overflow: ellipsis;
				cursor: pinter;
				&:hover{
					border-bottom: 1px solid $border-color-hover;
				}
				&:focus{
					border-bottom: 1px solid $border-color-active;
					outline: -webkit-focus-ring-color auto 0;
				}
			}
		}
	}
}

.main-content{
	height: 100vh;
	.card-project-link{
		position: absolute;
		right: 0;
		width: 4rem;
		height: 100%;
		display: -ms-flexbox;
		display: flex;
		-ms-flex-align: center !important;
		align-items: center !important;
		font-size: 1.5rem;
		text-align: center;
		margin: 0;
		justify-content: center!important;
	}
}

.card-project{
	position: relative;
	.chart{
		height: 20rem;
		width: 16rem;
	}
}

// fixes
.btn:not(.bg-transparent):not([class*='btn-outline']):not(.btn-light):not(.btn-link):not([class*='alpha-']):not(.fab-menu-btn):hover{
	box-shadow: 0 0 0 0 !important;
}

section{
	&.project-hero{
		.datapicker{
			position: absolute;
			top: 3rem;
			right: 3rem;
			width: 15rem;
		}
	}
	&.project-steps{
		.badge{
			position: absolute;
			right: 1rem;
			top: 1.5rem;
		}
	}
}



// FORM

.form-group{
}

.input-group-alternative {
	transition: box-shadow .15s ease;
	border: 0;
	box-shadow: none;
	border: 1px solid rgba($border-color-normal,.25);
	border-radius: 5px;
}
.input-group-text i {
	font-size: 1rem;
}

.datapicker{
	input{
		border: 0;
		font-size: $fs-s;
		width: 50%;	
		max-width: 50%;
		&:focus{
			background-color: rgba($info,.1);
			border-radius: 5px;
			outline: 0;
		}
	}
	i{
		margin: 0 1rem;
	}
}


// Loader
.pace {
  -webkit-pointer-events: none;
  pointer-events: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  user-select: none;
}

.pace-inactive {
  display: none;
}

.pace .pace-progress {
  background: #29d;
  position: fixed;
  z-index: 2000;
  top: 0;
  right: 100%;
  width: 100%;
  height: 2px;
}

.pace .pace-progress-inner {
  display: block;
  position: absolute;
  right: 0px;
  width: 100px;
  height: 100%;
  box-shadow: 0 0 10px #29d, 0 0 5px #29d;
  opacity: 1.0;
  -webkit-transform: rotate(3deg) translate(0px, -4px);
  -moz-transform: rotate(3deg) translate(0px, -4px);
  -ms-transform: rotate(3deg) translate(0px, -4px);
  -o-transform: rotate(3deg) translate(0px, -4px);
  transform: rotate(3deg) translate(0px, -4px);
}

.pace .pace-activity {
  display: block;
  position: fixed;
  z-index: 2000;
  top: 15px;
  right: 15px;
  width: 14px;
  height: 14px;
  border: solid 2px transparent;
  border-top-color: #29d;
  border-left-color: #29d;
  border-radius: 10px;
  -webkit-animation: pace-spinner 400ms linear infinite;
  -moz-animation: pace-spinner 400ms linear infinite;
  -ms-animation: pace-spinner 400ms linear infinite;
  -o-animation: pace-spinner 400ms linear infinite;
  animation: pace-spinner 400ms linear infinite;
}

@-webkit-keyframes pace-spinner {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
@-moz-keyframes pace-spinner {
  0% { -moz-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -moz-transform: rotate(360deg); transform: rotate(360deg); }
}
@-o-keyframes pace-spinner {
  0% { -o-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -o-transform: rotate(360deg); transform: rotate(360deg); }
}
@-ms-keyframes pace-spinner {
  0% { -ms-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -ms-transform: rotate(360deg); transform: rotate(360deg); }
}
@keyframes pace-spinner {
  0% { transform: rotate(0deg); transform: rotate(0deg); }
  100% { transform: rotate(360deg); transform: rotate(360deg); }
}


